.personal {
  color: white;
  text-decoration: none;
  position: absolute;
  bottom: 15px;
  width: 100%;
  margin: 0 auto;
}

.enterButton {
  border-radius: 1rem;
  font-family: "Fantasy", "marker felt";
  margin-right: auto;
  text-align: center !important;
  background: none;
  color: #fff;
  border: 3px solid #008eff;
  position: relative;
  z-index: 2;
  height: 60px;
  font-size: 1.6em;
  padding: 0;
  cursor: pointer;
  transition: 800ms ease all;
  outline: none;
  white-space: nowrap;
  width: 30%;
  margin: 0 auto;
  margin-top: 30vw;
  letter-spacing: 0.3rem;
}

.enterButton:hover {
  background: #fff;
  color: #008eff;
  border-radius: 1rem !important;
}

.enterButton:before,
.enterButton:after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  height: 2px;
  width: 0;
  background: #008eff;
  transition: 400ms ease all;
  border-radius: 1rem !important;
}
.enterButton:after {
  right: inherit;
  top: inherit;
  left: 0;
  bottom: 0;
  border-radius: 1rem !important;
}
.enterButton:hover:before,
.enterButton:hover:after {
  width: 100%;
  transition: 800ms ease all;
  border-radius: 1rem !important;
}

.quote {
  /* font-family: 'Nothing You Could Do', cursive; */
  font-family: "Fantasy", "marker felt";
  width: 100%;
  margin: 0 auto;
  text-align: center;
  margin-top: 15%;
  margin-bottom: 1%;
  color: white;
  font-size: 50px;
  font-weight: 800;
  position: absolute;
  z-index: 2;
}

